import React from "react";
import "./tracker.css";
import "../../css/normalize.css";
import "../../css/global.css";
import logo from "../../images/logo.svg" 
import { Controller, Scene } from "react-scrollmagic"
import Fade from 'react-reveal/Fade';

export default props => (
    <>
        <Fade bottom duration={300}>
            <Controller>
                <Scene triggerHook="onLeave" pin>
                    <section className="pinned">
                        <div className="tracker">
                            <img className="tracker__logo" src={logo} alt=""/>
                            <h4 className="tracker__head">{props.trackerHead} </h4>
                        </div>
                        <div className="phases">
                            <Controller>
                                <Scene  duration={() => { if (document.getElementById('trigger') != null) return document.getElementById('trigger').clientHeight } }
                                        classToggle="phases__active" triggerElement="#trigger" indicators={true}>
                                    <h3 className="phases__txt">PHASE 1: BOOBIE</h3>
                                </Scene>
                            </Controller>
                            <h3 className="phases__txt">PHASE 2: TITTY</h3>
                            <h3 className="phases__txt">PHASE 3: BUMHOLE</h3>
                            <h3 className="phases__txt">PHASE 4: MAMA</h3>
                            <h3 className="phases__txt">PHASE 5: LICK</h3>
                            <h3 className="phases__txt">PHASE 6: BOOTY</h3>
                        </div>
                    </section>
                </Scene>
            </Controller>
        </Fade>
    </>

    
);  
